

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.hero {
    background-image: url('../../dist/img/capa_hero.jpg');
    background-size: cover;
    height: 100vh;
    width: 100%;
    background-position: center top;

}

.sobre {

    &__container {
        display: grid;
        grid-template-columns: 52% 48%;
        position: relative;

        h2 {
            text-align: center;
            margin-top: 20px;
        }

        p {
            margin: 60px 10px;
        }

        &__preto {
            background-color: black;
            color: white;

            &__relativo {
                position: absolute;
                z-index: 1;
                text-align: center;
                right: 40%;
                top: 76%;

                span {
                    color: black;
                }
            }
        }

        &__branco {
            background-color: white;
            color: black;
        }

        &__streaming {
            display: flex;
            justify-content: space-around;
            margin-top: 140px;

            
            a {
                transition: 1s ease;
            }

            a:hover, .imagem_link:hover {
                transition: 1s ease;
                border-radius: 0%;
                height: 55px;
            }
        }
    }
}
.imagem_link {
    height: 50px;
    border-radius: 30%;
    transition: 1s ease;
}

.critica {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
}
.rotten {
    margin-top: 20px;
    border-radius: 5%;
    max-width: 100%;
}